<template>
  <div class="edit-page">
    <div class="ml-l pl-l mb-l pb-l">
      <el-button @click="$router.back()" size="small">返回</el-button>
    </div>
    <el-form :model="form" :rules="rules" label-width="120px" ref="form" size="small">
      <div class="container">
        <h3 class="title">基本信息</h3>
        <!-- 商品名称 -->
        <el-form-item label="商品名">
          <el-col span="6">
            <el-input v-model="form.name"></el-input>
          </el-col>
        </el-form-item>
        <!-- 商品描述 -->
        <el-form-item label="商品描述">
          <el-col span="12">
            <el-input v-model="form.description"></el-input>
          </el-col>
        </el-form-item>
        <!-- 添加商品图 -->
        <el-form-item label="添加商品图">
          <upload-file :limit="10" accept="image/*" v-model="form.imagesList"></upload-file>
          <div class="tips">建议尺寸：800*800像素，最多上传10张</div>
        </el-form-item>
        <el-form-item label="商品详情">
          <upload-file :limit="10" accept="image/*" v-model="form.descriptionimagesList"></upload-file>
          <div class="tips upload">上传商品详情页图片，帮助买家进一步了解商品。</div>
          <div class="tips nopt">建议图片宽度不大于1500px，质量不大于2M，最多可上传10张。</div>
        </el-form-item>
        <!-- 选择商品分类 -->
        <el-form-item label="选择分类">
          <el-row>
            <el-col :span="6">
              <el-select placeholder="请选择分类" v-model="form.categoryidlistList" multiple>
                <el-option :key="category.id" :label="category.name.slice(1).split('/')[0]" :value="category.id" v-for="category in categories"></el-option>
              </el-select>
            </el-col>
            <el-col :span="2" style="margin-left: 15px;">
              <el-button @click="$router.push({ name: 'category' })" size="small" type="text">新建类目</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </div>

      <!-- 时段 -->
      <div class="container">
        <el-switch :inactive-text="'启用售卖时段'" v-model="form.withtimeslot" :active-value="2" :inactive-value="1" />
        <div v-if="form.withtimeslot == 2">
          <div class="time-slot" v-for="(timeSlot, timeSlotIndex) in form.producttimeslotlistList" :key="timeSlotIndex">
            <i @click="onClickRemoveTimeSlot(timeSlotIndex)" class="el-icon-circle-close time-slot_remove"></i>
            <el-row gutter="8" type="flex" justify="center" align="center">
              <el-col :span="3" style="color: #666; font-size: 14px; margin-top: 2px; margin-left: 4px;">选择日期</el-col>
              <el-col :span="21">
                <el-checkbox-group @change="onChangeCheckBox(timeSlotIndex, $event)" v-model="timeSlot.weeklistList">
                  <el-checkbox v-for="week in weeklist" :disabled="disabledWeek.indexOf(week) !== -1 && (selectedWeek[timeSlotIndex] || []).indexOf(week) === -1" :key="week" :label="week"></el-checkbox>
                </el-checkbox-group>
              </el-col>
            </el-row>
            <el-row style="height: 30px; line-height: 30px; margin-top: 10px;" gutter="8" type="flex" justify="center" align="center">
              <el-col class="time-slot_title" :span="9">可用时段</el-col>
              <el-col class="time-slot_title" :span="2">可用时长</el-col>
              <el-col class="time-slot_title" :span="2">原价</el-col>
              <el-col class="time-slot_title" :span="2">折扣价</el-col>
              <el-col class="time-slot_title" :span="2">会员价</el-col>
              <el-col class="time-slot_title" :span="2" v-if="form.groupuserquantity > 0">团购价</el-col>
              <el-col class="time-slot_title" :span="2">成本价</el-col>
              <el-col class="time-slot_title" :span="2">开启售卖</el-col>
              <el-col class="time-slot_title" :span="1"></el-col>
            </el-row>
            <el-row style="margin-top: 5px;" gutter="8" type="flex" justify="center" align="center" v-for="(item, index) in timeSlot.producttimeslotpricelistList" :key="index">
              <el-col :span="9">
                <el-col :span="9">
                  <el-time-select
                    size="small"
                    style="width: 100%;"
                    placeholder="起始时间"
                    v-model="item.begintime"
                    :picker-options="{
                      start: '00:00',
                      step: '00:15',
                      end: '23:59'
                    }"
                  ></el-time-select>
                </el-col>
                <el-col :span="9">
                  <el-time-select
                    size="small"
                    style="width: 100%;"
                    placeholder="结束时间"
                    v-model="item.endtime"
                    :picker-options="{
                      start: '00:00',
                      step: '00:15',
                      end: '23:59',
                      minTime: item.nextday ? '' : item.begintime
                    }"
                  ></el-time-select>
                </el-col>
                <el-col class="time-slot_title" :span="6" style="margin-top: 5px;">
                  <el-switch size="small" v-model="item.nextday" active-text="次日"></el-switch>
                </el-col>
              </el-col>
              <el-col :span="2">
                <el-input size="small" v-model="item.duration" style="width: 100%;" placeholder="小时"></el-input>
              </el-col>
              <el-col :span="2">
                <el-input size="small" v-model="item.price" style="width: 100%;" placeholder="原价"></el-input>
              </el-col>
              <el-col :span="2">
                <el-input size="small" v-model="item.discountprice" style="width: 100%;" placeholder="折扣价"></el-input>
              </el-col>
              <el-col :span="2">
                <el-input size="small" v-model="item.vipprice" style="width: 100%;" placeholder="会员价"></el-input>
              </el-col>
              <el-col :span="2" v-if="form.groupuserquantity > 0">
                <el-input size="small" v-model="item.groupprice" style="width: 100%;" placeholder="团购价"></el-input>
              </el-col>
              <el-col :span="2">
                <el-input size="small" v-model="item.costprice" style="width: 100%;" placeholder="成本价"></el-input>
              </el-col>
              <el-col :span="2" style="display:flex;flex-direction:row;justify-content:center">
                <el-switch v-model="item.enabled" :active-value="2" :inactive-value="1" />
              </el-col>
              <el-col :span="1" class="time-slot_title" style="margin-top: 5px;">
                <i v-if="index === timeSlot.producttimeslotpricelistList.length - 1" @click="onClickAddTimeSlotPriceItem(timeSlotIndex)" class="el-icon-circle-plus" style="font-size: 20px;"></i>
                <i v-else @click="onClickRemoveTimeSlotPriceItem(timeSlotIndex, index)" class="el-icon-remove-outline" style="font-size: 20px;"></i>
              </el-col>
            </el-row>
          </div>
        </div>
        <el-button class="time-slot_add" @click="onClickAddTimeSlot" v-if="form.withtimeslot == 2">添加</el-button>
      </div>
      <!-- /时段 -->

      <div class="container">
        <h3 class="title">价格</h3>

        <el-form-item class="form-item-price" label="商品价格" v-show="form.withtimeslot == 1">
          <el-col span="4">
            <el-input v-model="form.price">
              <template slot="append"
                >元</template
              >
            </el-input>
          </el-col>
        </el-form-item>

        <el-form-item class="form-item-price" label="折扣价格" v-show="form.withtimeslot == 1">
          <el-row>
            <el-col span="4">
              <el-input v-model="form.discountprice">
                <template slot="append"
                  >元</template
                >
              </el-input>
            </el-col>
          </el-row>
          <div class="tips">设置该商品的折扣价格, 不填或填0代表不设置折扣价。</div>
        </el-form-item>

        <el-form-item class="form-item-price" label="会员价格" v-show="form.withtimeslot == 1">
          <el-row>
            <el-col span="4">
              <el-input v-model="form.vipprice">
                <template slot="append"
                  >元</template
                >
              </el-input>
            </el-col>
          </el-row>
          <div class="tips">设置该商品会员享有的价格, 不填或填0代表不设置会员价。</div>
        </el-form-item>

        <el-form-item label="成团人数">
          <el-row>
            <el-col span="4">
              <el-select v-model="form.groupuserquantity">
                <el-option :label="'不选择团购人数'" :value="0"></el-option>
                <el-option :label="'2人'" :value="2"></el-option>
                <el-option :label="'3人'" :value="3"></el-option>
                <el-option :label="'4人'" :value="4"></el-option>
                <el-option :label="'5人'" :value="5"></el-option>
              </el-select>
            </el-col>
          </el-row>
          <div class="tips">如不支持团购则不选择</div>
        </el-form-item>

        <el-form-item class="form-item-price" label="团购价格" v-show="!(form.withtimeslot == 2 || form.groupuserquantity == 0)">
          <el-row>
            <el-col span="4">
              <el-input v-model="form.groupprice">
                <template slot="append"
                  >元</template
                >
              </el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item label="商品编码">
          <el-row>
            <el-col span="4">
              <el-input v-model="form.number"></el-input>
            </el-col>
          </el-row>
          <div class="tips">如无则不填</div>
        </el-form-item>

        <el-form-item class="form-item-price" label="成本价格" prop="costprice" v-show="form.withtimeslot == 1">
          <el-row>
            <el-col span="4">
              <el-input v-model="form.costprice">
                <template slot="append"
                  >元</template
                >
              </el-input>
            </el-col>
          </el-row>
          <div class="tips">成本价将用于利润统计，方便您了解经营收益。</div>
        </el-form-item>

        <el-form-item>
          <el-col span="5">
            <el-switch :active-text="'上架商品'" v-model="form.online" :active-value="1" :inactive-value="0" />
          </el-col>
          <el-col span="18">
            <div class="tips" style="margin-left: 5px; margin-top: 5px;" v-if="form.online === 0">当前商品已下架。</div>
            <div class="tips" style="margin-left: 5px; margin-top: 5px;" v-else>当前商品已上架。</div>
          </el-col>
        </el-form-item>
      </div>
    </el-form>
    <div style="height: 100px;"></div>
    <div class="buttons">
      <el-button @click="onSubmit" type="primary">发布</el-button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import uploadFile from '../../../components/uploadFile/index.vue';
import { M } from '../../../service/grpc.js';
import { rpc } from '../../../service/index.js';
import { getToken, sortObject } from '../../../service/helper.js';
import { getCachedStore } from '../../../router/auth';

const weekMap = { 星期一: 1, 星期二: 2, 星期三: 3, 星期四: 4, 星期五: 5, 星期六: 6, 星期天: 7 };
const weekMap1 = { 1: '星期一', 2: '星期二', 3: '星期三', 4: '星期四', 5: '星期五', 6: '星期六', 7: '星期天' };

export default {
  components: {
    'upload-file': uploadFile,
  },
  data() {
    return {
      showProDialog: false,
      disabledWeek: [],
      selectedWeek: [],
      weeklist: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期天'],
      categories: [],
      display: true,
      form: {
        name: '',
        categoryid: '',
        videoimage: '',
        video: '',
        imagesList: [],
        description: '',
        descriptionimagesList: [],
        supportgroup: 0,
        groupuserquantity: 0,
        quantity: 0,
        price: 0,
        discountprice: 0,
        groupprice: 0,
        withoption: 0,
        optionsList: [],
        online: 1,
        number: '',
        costprice: 0,
        withquantity: 2,
        productdescriptionlong: '',
        productnoticelistList: [],
        supportreserve: 1,
        vipprice: 0,
        withtimeslot: 1,
        producttimeslotlistList: [
          // {
          //     weeklistList: [0],
          //     producttimeslotpricelistList: [{}],
          // },
        ],

        options: [],
        optionsDetail: []
      },
      token: '',
    };
  },
  watch: {
    'form.options': {
      deep: true,
      handler() {
        this.form.optionsDetail = this.buildOptionJson(this.form.options);
      }
    },
    'form.withtimeslot': function(v) {
      if (v == 2 && this.form.producttimeslotlistList.length === 0) {
        this.onClickAddTimeSlot();
      }
    },
    'form.producttimeslotlistList': function(v) {
      if (v.length === 0) {
        this.form.withtimeslot = 1;
      }
    }
  },
  async created() {
    this.token = getToken();
    var store = await getCachedStore();

    if (this.$route.query.type === 'edit') {
      rpc(
        'StoreProductService.GetProduct',
        M('IDWithTokenRequest', {
          token: M('Token', { token: this.token }),
          id: this.$route.query.id
        })
      ).then(res => {
        this.form = this.backend2frontend(res);

        this.form.price = this.$amount1(this.form.price);
        this.form.discountprice = this.$amount1(this.form.discountprice);
        this.form.groupprice = this.$amount1(this.form.groupprice);
        this.form.costprice = this.$amount1(this.form.costprice);
        this.form.vipprice = this.$amount1(this.form.vipprice);
        this.form.optionsDetail = this.form.optionsDetail.map(item => ({
          ...item,
          price: this.$amount1(item.price),
          discountprice: this.$amount1(item.discountprice),
          groupprice: this.$amount1(item.groupprice),
          costprice: this.$amount1(item.costprice),
          vipprice: this.$amount1(item.vipprice)
        }));
        this.form.producttimeslotlistList = this.form.producttimeslotlistList.map(item => ({
          weeklistList: item.weeklistList.map(v => weekMap1[v]),
          producttimeslotpricelistList: item.producttimeslotpricelistList.map(v => ({
            ...v,
            endtime: v.endtime.startsWith(':') ? v.endtime.slice(1) : v.endtime,
            nextday: v.endtime.startsWith(':'),
            duration: v.duration / 60 / 60,
            price: v.price / 100,
            discountprice: v.discountprice / 100,
            vipprice: v.vipprice / 100,
            groupprice: v.groupprice / 100,
            costprice: v.costprice / 100
          }))
        }));
      });
    }
    rpc(
      'StoreCategoryService.GetCategoryList',
      M('CategoryListQueryRequest', {
        token: M('Token', { token: this.token }),
        ishidden: 2,
        level: 1,
        count: 100,
        sortby: 1
      })
    ).then(res => {
      this.categories = res.categorylistList;
    });
  },
  methods: {
    getDetailDesc(key) {
      if (key === 'discountprice') return '优惠价格, 不填或填0代表不设置折扣价';
      if (key === 'groupprice') return '如不支持团购则不填';
      return '';
    },
    onSubmit() {
      this.$refs.form.validate(status => {
        if (!status) return;
        let opt = JSON.parse(JSON.stringify(this.form));
        opt.price = this.$amount2(opt.price);
        opt.discountprice = this.$amount2(opt.discountprice);
        opt.groupprice = this.$amount2(opt.groupprice);
        opt.costprice = this.$amount2(opt.costprice);
        opt.vipprice = this.$amount2(opt.vipprice);
        opt.optionsDetail = opt.optionsDetail.map(item => ({
          ...item,
          price: this.$amount2(item.price),
          discountprice: this.$amount2(item.discountprice),
          groupprice: this.$amount2(item.groupprice),
          costprice: this.$amount2(item.costprice),
          vipprice: this.$amount2(item.vipprice)
        }));
        opt.producttimeslotlistList = opt.producttimeslotlistList.map(item => ({
          weeklistList: item.weeklistList.map(v => weekMap[v]),
          producttimeslotpricelistList: item.producttimeslotpricelistList.map(v => ({
            ...v,
            endtime: v.nextday ? ':' + v.endtime : v.endtime,
            duration: parseInt(v.duration * 60 * 60),
            price: this.$amount2(v.price),
            discountprice: this.$amount2(v.discountprice),
            vipprice: this.$amount2(v.vipprice),
            groupprice: this.$amount2(v.groupprice),
            costprice: this.$amount2(v.costprice)
          }))
        }));
        opt = this.frontend2backend(opt);

        if (!opt.categoryidlistList.length) {
          this.$message({
            message: '忘记选择分类了',
            type: 'error'
          });
          return;
        }
        // 兼容categoryid, 后期小程序端改为取categoryidlistList字段时, 即可去掉此兼容代码
        opt.categoryid = opt.categoryidlistList[0];

        // 检查价格
        if (opt.withtimeslot == 1) {
          if (opt.price == 0) {
            this.$message({
              message: '忘记设置原价了',
              type: 'error'
            });
            return;
          }
          if (opt.discountprice != 0 && opt.discountprice >= opt.price) {
            this.$message({
              message: '如果设置折扣价, 则折扣价需小于原价',
              type: 'error'
            });
            return;
          }
          if (opt.vipprice != 0) {
            if (opt.vipprice >= opt.price) {
              this.$message({
                message: '如果设置会员价, 则会员价需小于原价',
                type: 'error'
              });
              return;
            }
            if (opt.discountprice != 0 && opt.vipprice >= opt.discountprice) {
              this.$message({
                message: '会员价应该小于折扣价',
                type: 'error'
              });
              return;
            }
          }
          if (opt.supportgroup == 1) {
            if (opt.groupprice == 0) {
              this.$message({
                message: '忘记设置团购价了',
                type: 'error'
              });
              return;
            }
            if (opt.groupprice >= opt.price) {
              this.$message({
                message: '团购价需小于原价',
                type: 'error'
              });
              return;
            }
            if (opt.discountprice != 0 && opt.groupprice >= opt.discountprice) {
              this.$message({
                message: '团购价需小于折扣价',
                type: 'error'
              });
              return;
            }
            if (opt.vipprice != 0 && opt.groupprice >= opt.vipprice) {
              this.$message({
                message: '团购价需小于会员价',
                type: 'error'
              });
              return;
            }
          }
        }
        if (opt.withtimeslot == 2) {
          for (const itemmsg0 of opt.producttimeslotlistList) {
            for (const item of itemmsg0.toObject().producttimeslotpricelistList) {
              if (item.price == 0) {
                this.$message({
                  message: '忘记设置原价了',
                  type: 'error'
                });
                return;
              }
              if (item.discountprice != 0 && item.discountprice >= item.price) {
                this.$message({
                  message: '如果设置折扣价, 则折扣价需小于原价',
                  type: 'error'
                });
                return;
              }
              if (item.vipprice != 0) {
                if (item.vipprice >= item.price) {
                  this.$message({
                    message: '如果设置会员价, 则会员价需小于原价',
                    type: 'error'
                  });
                  return;
                }
                if (item.discountprice != 0 && item.vipprice >= item.discountprice) {
                  this.$message({
                    message: '会员价应该小于折扣价',
                    type: 'error'
                  });
                  return;
                }
              }
              if (opt.supportgroup == 1) {
                if (item.groupprice == 0) {
                  this.$message({
                    message: '忘记设置团购价了',
                    type: 'error'
                  });
                  return;
                }
                if (item.groupprice >= item.price) {
                  this.$message({
                    message: '团购价需小于原价',
                    type: 'error'
                  });
                  return;
                }
                if (item.discountprice != 0 && item.groupprice >= item.discountprice) {
                  this.$message({
                    message: '团购价需小于折扣价',
                    type: 'error'
                  });
                  return;
                }
                if (item.vipprice != 0 && item.groupprice >= item.vipprice) {
                  this.$message({
                    message: '团购价需小于会员价',
                    type: 'error'
                  });
                  return;
                }
              }
            }
          }
        }

        if (this.$route.query.type === 'edit') {
          rpc(
            'StoreProductService.UpdateProduct',
            M('ProductWithTokenRequest', {
              token: M('Token', { token: this.token }),
              product: M('Product', opt)
            })
          ).then(res => {
            this.$message({
              message: '商品保存成功',
              type: 'success'
            });
          });
          return;
        }

        rpc(
          'StoreProductService.CreateProduct',
          M('ProductWithTokenRequest', {
            token: M('Token', { token: this.token }),
            product: M('Product', opt)
          })
        ).then(res => {
          this.$router.back();
        });
      });
    },

    // 将后端的product变成前端的product, 主要是多规格才搞的这个方法
    backend2frontend(product) {
      var options = [];
      var optionsDetail = [];
      var nv = {};
      for (var i = 0; i < product.optionsList.length; i++) {
        var tmp2 = {
          price: product.optionsList[i].price,
          discountprice: product.optionsList[i].discountprice,
          groupprice: product.optionsList[i].groupprice,
          costprice: product.optionsList[i].costprice,
          vipprice: product.optionsList[i].vipprice,
          quantity: product.optionsList[i].quantity
        };
        var tmp1 = JSON.parse(product.optionsList[i].name);
        for (var k in tmp1) {
          if (!nv.hasOwnProperty(k)) {
            nv[k] = [];
          }
          if (nv[k].indexOf(tmp1[k]) === -1) {
            nv[k].push(tmp1[k]);
          }
          tmp2[k] = tmp1[k];
        }
        optionsDetail.push(tmp2);
      }
      product.optionsDetail = optionsDetail;
      for (var k in nv) {
        options.push({
          vid: k,
          name: k,
          value: nv[k].map(v => {
            return {
              vid: k + ':' + v,
              value: v
            };
          })
        });
      }
      product.options = options;
      return product;
    },

    // 将前端的product变成后端的product, 主要是多规格才搞的这个方法
    frontend2backend(product) {
      product.supportgroup = product.groupuserquantity > 0 ? 1 : 0;
      product.withoption = 0;
      product.optionsList = [];
      if (product.optionsDetail.length) {
        product.withoption = 1;
        product.optionsList = product.optionsDetail.map(v => {
          var op = {};
          var tmp = {};
          for (var k in v) {
            if (k === 'price') {
              op[k] = v[k];
              continue;
            }
            if (k === 'discountprice') {
              op[k] = v[k];
              continue;
            }
            if (k === 'vipprice') {
              op[k] = v[k];
              continue;
            }
            if (k === 'groupprice') {
              op[k] = v[k];
              continue;
            }
            if (k === 'quantity') {
              op[k] = v[k];
              continue;
            }
            if (k === 'costprice') {
              op[k] = v[k];
              continue;
            }
            tmp[k] = v[k];
          }
          op.name = JSON.stringify(sortObject(tmp));
          return M('Option', op);
        });
      }
      product.productnoticelistList = product.productnoticelistList.map(item => M('ProductNotice', item));
      product.producttimeslotlistList = product.producttimeslotlistList.map(item =>
        M('ProductTimeSlot', {
          weeklistList: item.weeklistList,
          producttimeslotpricelistList: item.producttimeslotpricelistList.map(item1 => M('ProductTimeSlotPrice', item1))
        })
      );
      return product;
    },
    getTitle(k) {
      const map = {
        firstValue: '-',
        secondValue: '-',
        thirdValue: '-',
        price: '价格（元）',
        discountprice: '折扣价（元）',
        vipprice: '会员价（元）',
        groupprice: '团购价（元）',
        quantity: '库存',
        costprice: '成本价（元）'
      };
      return map[k] ? map[k] : k;
    },
    getTitleInfo(k) {
      const map = {
        price: '原价',
        discountprice: '当有折扣价时，商品优先显示折扣价，原件则作为划线价展示, 不填或填0代表不设置折扣价',
        vipprice: '当有会员价时，会员会使用会员价, 不填或填0代表不设置会员价',
        groupprice: '拼团使用的价格',
        quantity: '商品库存',
        costprice: '用于统计商店利润'
      };
      return map[k];
    },
    removeOption(index) {
      this.form.options.splice(index, 1);
    },
    addOption() {
      this.form.options.push({
        name: '',
        value: [{ value: '', uid: Math.random() }],
        uid: Math.random()
      });
    },
    removeOptionItem(from, index) {
      from.splice(index, 1);
    },
    addOptionItem(value) {
      value.push({ value: '', uid: Math.random() });
    },
    buildOptionJson(data) {
      // const a = [{ name: '颜色', value: ['红', '黄'] }, { name: '', value: ['大'] }, { name: '尺码', value: ['S', 'M'] }];
      let result = [];
      const map = ['firstValue', 'secondValue', 'thirdValue'];

      data.forEach((option, index) => {
        if (!option.value.length) return;

        const name = option.name || map[index];

        if (!result.length) {
          result = option.value.map(v => {
            const x = {};
            x[name] = v.value;
            return x;
          });
        } else {
          const r = [];
          result.forEach(item => {
            option.value.forEach(v => {
              const x = { ...item };
              x[name] = v.value;
              r.push(x);
            });
          });
          result = r;
        }
      });

      const arr = this.form.optionsDetail;

      const rs = result.map((item, index) => {
        const { price = 0, discountprice = 0, vipprice = 0, groupprice = 0, quantity = 0, costprice = 0 } = arr[index] || {};
        return { ...item, price, discountprice, vipprice, groupprice, quantity, costprice };
      });

      return rs;
    },
    onClickAddTimeSlotPriceItem(slotIndex) {
      this.form.producttimeslotlistList[slotIndex].producttimeslotpricelistList.push({
        begintime: '',
        endtime: '',
        duration: '',
        price: '',
        discountprice: '',
        groupprice: '',
        vipprice: '',
        nextday: false,
        enabled: 2
      });
    },
    onClickRemoveTimeSlotPriceItem(slotIndex, index) {
      this.form.producttimeslotlistList[slotIndex].producttimeslotpricelistList.splice(index, 1);
    },
    onClickAddTimeSlot() {
      this.form.producttimeslotlistList.push({
        weeklistList: [],
        producttimeslotpricelistList: [
          {
            begintime: '',
            endtime: '',
            duration: '',
            price: '',
            discountprice: '',
            groupprice: '',
            vipprice: '',
            costprice: '',
            nextday: false,
            enabled: 2
          }
        ]
      });
    },
    onClickRemoveTimeSlot(slotIndex) {
      this.form.producttimeslotlistList.splice(slotIndex, 1);
      this.selectedWeek.splice(slotIndex, 1);
      this.disabledWeekHandle();
    },
    onChangeCheckBox(index, e) {
      this.selectedWeek[index] = e;
      this.disabledWeekHandle();
    },
    disabledWeekHandle() {
      this.disabledWeek = Object.values(this.selectedWeek)
        .filter(item => item)
        .reduce((a, b) => a.concat(b), []);
    }
  }
};
</script>

